
import { getList } from 'api/article'
import slider from 'vue-concise-slider'
import ContentHeader from '../components/content-header'
import { mapGetters } from 'vuex'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
name: 'index',
components: {
ContentHeader,
slider,
XButton
},
data () {
return {
vist_list: [
{logo:'./static/img/logo_1.png',name:'EOS'}
],
member_list: [
{logo:'./static/img/avatar_10.png',name:'RENDAN<br>BLUMER',job:'CEO'},
{logo:'./static/img/avatar_9.png',name:'DANEL<br>LARMER',job:'CTO'},
{logo:'./static/img/avatar_8.png',name:'RENDAN<br>BLUMER',job:'CFO'},
{logo:'./static/img/avatar_7.png',name:'XIAO LAI<br>LI',job:'合伙人'}
],
coin_people_list: ['./static/img/avatar_1.png', './static/img/avatar_2.png', './static/img/avatar_3.png', './static/img/avatar_4.png', './static/img/avatar_5.png', './static/img/avatar_6.png'],
timeArr: ['2018年5月7日', '2018年6月15日', '2018年6月16日', '2018年6月21日'],
time: '',
pages:[
{
html: `<div class="slider">
<p>EOS主网启动 ，前21个节点已出炉，6个中国节点成功入选，六个超级节点分别为第5名的eos佳能、第11名的火币矿池、第12名的EOS引力区、第17名...</p>
<p class="title">由幸福的刘基谈编辑</p>
</div>`,
},
{
html: `<div class="slider">
<p>Block.one首席执行官丹尼尔•拉里默（Daniel Larimer）已经正式发布了面向分布式应用（DApps）的EOSIO平台架构最新版本——EOSIO Dawn 4.0...</p>
<p class="title">由幸福的刘基谈编辑</p>
</div>`,
},
{
html: `<div class="slider">
<p>EOS主网上线不到48小时，6月16日下午6点左右，EOS共识协议中的一个漏洞使其网络完全冻结了5个小时，主网崩溃，出块暂停。EOS社区负责人Tho...</p>
<p class="title">由幸福的刘基谈编辑</p>
</div>`,
},
{
html: `<div class="slider">
<p>ECAF（EOS核心仲裁论坛）最新会议内容：“保障财产不可侵犯”，针对用户出现的EOS资产被盗问题，发布了相应解决方案，用户如发现账户资产异常...</p>
<p class="title">由幸福的刘基谈编辑</p>
</div>`,
}
],
sliderinit: {
currentPage: 0,
thresholdDistance: 100,
thresholdTime: 300,
loop:true,
infinite:1,
slidesToScroll:1
},
join_status: false,
community: '加入社区',
haveMeanus: false,
count: '1024',
meanusTxt: '展开资料',
curent: 0,
naves :['全部','技术','观点','解读'],
listQuery: {
page: 1,
limit: 10,
type: 'EOS',
aspect: '',
id:''
},
list:  [],
total: null,
}
},
created() {
this.$store.dispatch('InitJoin', '')
this.getList();
},
computed: {
...mapGetters([
'join',
])
},
methods: {
getList() {
this.community = this.join ? '已加入' : '加入社区'
this.join_status = this.join
getList(this.listQuery).then(response => {
this.list = response.data.items;
this.total = response.data.total;
})
},
onInit (data) {
this.time = this.timeArr[data.currentPage]
},
slide (data) {
let len = this.timeArr.length
let current
if( data.currentPage == len ) {
current = 0
} else if (data.currentPage == '-1') {
current = len - 1
} else {
current = data.currentPage
}
this.time = this.timeArr[current]
},
onItemClick (index) {
this.curent = index
let aspect = index == '0'? '': this.naves[index]
this.listQuery.aspect = aspect
getList(this.listQuery).then(response => {
this.list = response.data.items;
this.total = response.data.total;
})
},
onDevoteClick (){
this.$vux.toast.text('功能暂未开放', 'middle');
},
onOpenMeanus (){
this.haveMeanus = !this.haveMeanus
this.meanusTxt = this.haveMeanus ? '收起资料' : '展开资料'
},
communityAdd () {
var that= this
if (!this.join_status) {
that.$store.dispatch('UpdateJoin', {join: true}).then(() => {
this.join_status = true
this.community = '已加入'
this.$vux.toast.show({
type: 'success',
text: '社区加入成功',
})
})
}
}
}
}



